@use "../base/variables" as v;
@use "../base/mixins" as m;

.testimonial {
  color: v.$blanco;
  background-color: v.$secundario;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
  color: v.$blanco;

  &:last-child {
    margin-bottom: 0;
  }

  @include m.tablet {
    margin-bottom: 0;
    &:nth-child(1) {
      grid-column: 1 / 4;
    }
    &:nth-child(3) {
      grid-column: 5 / 6;
      grid-row: 1 / 3;
    }
    &:nth-child(4) {
      grid-column: 1 / 3;
    }
    &:nth-child(5) {
      grid-column: 3 / 5;
    }
  }

  &__header {
    display: flex;
    gap: 2rem;
    align-items: center;
  }

  &__imagen {
    flex-basis: 10rem;
    overflow: hidden;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }

  &__autor {
    color: v.$blanco;
    font-weight: 700;
    margin: 1.5rem 0;
  }

  &__texto {
    font-size: 2rem;
    line-height: 1.5;
    margin: 2rem 0;
    padding-left: 4rem;
    position: relative;

    &::before {
      content: '"';
      font-size: 10rem;
      position: absolute;
      left: 0;
      top: -4rem;
      opacity: 0.2;
      font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
    }
  }
}
